@charset "UTF-8";
/* You can add global styles to this file, and also import other style files */
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700;900&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Roboto", sans-serif;
  scroll-behavior: smooth;
}

:root {
  --color-black: #333;
  --color-black2: #333;
  --color-white: #fff;
  --color-white2: #fff;
}

.dark {
  --color-black: #fff;
  --color-black2: #444;
  --color-white: #333;
  --color-white2: #444;
}
.dark p {
  color: var(--color-black) !important;
}
.dark .themeSwitch::before {
  content: "ï†…" !important;
  font-family: fontAwesome !important;
}
.dark .themeSwitch::after {
  content: "Switch to Light mode" !important;
}
.dark .nav {
  border-left: 1px solid rgba(0, 0, 0, 0.5) !important;
}
.dark .main .topbar {
  border-bottom: 1px solid rgba(0, 0, 0, 0.5) !important;
}
.dark .btn:hover {
  background: #2196f3 !important;
}
.dark .socialMedias li a:hover {
  background: #2196f3 !important;
}
.dark .services .content .serviceBox img {
  filter: invert(1) !important;
}
.dark .services .content .serviceBox:hover {
  background: #2196f3 !important;
}
.dark .projects .content .workBox .textBx {
  background: #444 !important;
}
.dark .testimonials .content .testimonialsBox:hover {
  background: #2196f2 !important;
}
.dark .contact .contactForm .row2 input[type=submit]:hover {
  background: #2196f2 !important;
}

body {
  overflow-x: hidden;
}